@import './src/common/styles/mixins';

.main {
	@include flex(center, row, center);
	.content {
		@include flex(space-between, column, center);
		max-width: 1008px;
		min-width: 1008px;
		margin-top: 60px;
		@media screen and (max-width: 1024px) {
			width: auto;
			max-width: none;
			min-width: auto;
		}
		.headerBlock {
			display: flex;
			justify-content: space-between;
			align-items: center;
			width: 100%;
			height: 50px;
			button {
				width: 175px;
				height: 36px;
				border-radius: 30px;
				text-transform: none;
				font-weight: 500;
				font-size: 16px;
			}
		}
		.controlPanel {
			@include flex(space-between, row, flex-end);
			width: 100%;
			height: 62px;
			gap: 24px;
			margin-top: 42px;
			margin-bottom: 24px;
			@media screen and (max-width: 1024px) {
				flex-direction: column;
				align-items: center;
				height: auto;
				gap: 10px
			}
			.search {
				@include flex(space-between, column, center);
				@media screen and (max-width: 1024px) {
					h5 {
						margin-bottom: 10px;
					}
				}
			}
			h5 {
				margin: 0;
			}
			.reset {
				@include flex(space-between, column, center);
				width: 62px;
				height: 100%;
				@media screen and (max-width: 1024px) {
					display: none;
				}
				button {
					max-width: 45px;
					min-width: 45px;
					margin-top: 8px;
					height: 64px;
					border-radius: 50%;
					padding: 0;
				}
				span {
					height: 45px;
					width: 45px;
					display: block;
					text-align: center;
					margin: 0;
					svg {
						height: 100%;
						width: 100%;
						color: #1976d2;
					}
				}
			}
			.isOnwToggle {
				@include flex(space-between, column, center);
				width: 196px;
				height: 100%;
				@media screen and (max-width: 1024px) {
					h5 {
						margin-bottom: 10px;
					}

				}
				.buttonGroup {
					height: 36px;
					width: 100%;
					button {
						width: 100%;
					}
				}
			}
			.cardsCount {
				@include flex(space-between, column, center);
				height: 100%;
			}
		}
		.packsTable {
			width: 100%;
			@media screen and (max-width: 1024px) {
				th:nth-child(3), th:nth-child(4), td:nth-child(3), td:nth-child(4) {
					display: none;
				}
			}
			@media screen and (max-width: 600px) {
				td:nth-child(1), {
					min-width: auto;
				}
				th:nth-child(2), td:nth-child(2), {
					display: none;
				}
			}
				th {
					background-color: #efefef;
					font-weight: bold;
				}
				tr {
					height: 70px;
				}
				.packName {
					word-wrap: break-word;
					max-width: 290px;
					min-width: 290px;
					font-weight: bold;
					cursor: pointer;
					.cover {
						max-width: 100%;
						height: 100%;
						display: flex;
						align-items: center;
						gap: 10px;
						.title {
							max-width: 74%;
							height: 100%;
							word-wrap: break-word;
						}
					}
					img {
						max-height: 50px;
						max-width: 80px;
					}
				}
				.sortTableCell {
					height: 100%;
					.title {
						position: relative;
						display: inline-block;
					}
					svg {
						cursor: pointer;
						position: absolute;
						top: 0;
						right: -15px;
					}
				}
				.tableHeader {
					max-height: 48px;
					min-height: 48px;
				}
			}
		}
	}

	.buttonsGroup {
		height: 100%;
		width: 100%;
		@include flex(space-around, row, center);
		button {
			min-width: 20px;
			max-width: 20px;
			min-height: 20px;
			max-height: 20px;
			text-align: center;
			@include flex(center, row, center);
			span {
				display: block;
				min-width: 20px;
				max-width: 20px;
				min-height: 20px;
				max-height: 20px;
				text-align: center;
				margin: 0 auto;
				svg {
					height: 100%;
					width: 100%;
				}
			}
		}
	}

